<template>
  <div class="container">
    <div id="amap-container">
    </div>
    <div class="citySelect-container">
      <el-select v-model="province" v-on:change="search(province, 'province')" placeholder="请选择省">
        <el-option
          v-for="item in provinceList"
          :key="item.adcode"
          :label="item.name"
          :value="item.adcode">
        </el-option>
      </el-select>
      <el-select v-model="city" v-on:change="search(city, 'city')" placeholder="请选择市">
        <el-option
          v-for="item in cityList"
          :key="item.adcode"
          :label="item.name"
          :value="item.adcode">
        </el-option>
      </el-select>
      <el-select v-model="district" v-on:change="search(district, 'district')" placeholder="请选择区">
        <el-option
          v-for="item in districtList"
          :key="item.adcode"
          :label="item.name"
          :value="item.adcode">
        </el-option>
      </el-select>
      <el-select v-model="street" v-on:change="search(street, 'street')" placeholder="请选择街道">
        <el-option
          v-for="item in streetList"
          :key="item.adcode"
          :label="item.name"
          :value="item.adcode">
        </el-option>
      </el-select>
      <el-button type="primary" icon="el-icon-download" v-on:click="downloadGeo">{{ fileName || '下载' }}</el-button>
    </div>
    <a href=""></a>
  </div>
</template>

<script>
import AMap from 'AMap'
import FileSaver from 'file-saver'
var map, districtSearch
export default {
  name: 'aMap',
  data () {
    return {
      bounds: [],
      province: '',
      city: '',
      district: '',
      street: '',
      provinceList: [],
      cityList: [],
      districtList: [],
      streetList: [],
      fileName: ''
    }
  },
  methods: {
    // 初始化
    init: function () {
      map = new AMap.Map('amap-container', {
        zoom: 14,
        mapStyle: 'amap://styles/whitesmoke'
      })
      map.setCenter([116.397428, 39.90923])
      // 初始化行政区划搜索
      AMap.service('AMap.DistrictSearch', function () {
        // 在对象初始化的时候设定
        districtSearch = new AMap.DistrictSearch({
          subdistrict: 1, // 返回下一级行政区
          showbiz: false // 最后一级返回街道信息
        })
      })
      // 初始化省列表
      let _ = this
      districtSearch.search('中国', function (status, result) {
        if (status === 'complete') {
          _.getData(result.districtList[0])
        }
      })
    },
    // 获取边界和下级城市数据
    getData (data, level) {
      let _ = this
      if (level) {
        _.fileName = data.name
        // 清空下一级别的下拉列表
        _.clearSelectList(level)
      }
      // 边界
      _.bounds = data.boundaries
      if (_.bounds) {
        var len = _.bounds.length
        for (let i = 0; i < len; i++) {
          var polygon = new AMap.Polygon({
            strokeWeight: 1,
            strokeColor: '#CC66CC',
            fillColor: '#CCF3FF',
            fillOpacity: 0.5,
            path: _.bounds[i]
          })
          polygon.setMap(map)
        }
        map.setFitView() // 地图自适应
      }
      // 下级城市
      var subList = data.districtList
      if (subList) {
        var curLevel = subList[0].level
        if (curLevel === 'province') {
          _.provinceList = subList
        } else if (curLevel === 'city') {
          _.cityList = subList
        } else if (curLevel === 'district') {
          _.districtList = subList
        } else if (curLevel === 'street') {
          _.streetList = subList
        }
      }
    },
    // 清空列表
    clearSelectList: function (level) {
      let _ = this
      if (level === 'province') {
        _.cityList = []
        _.city = ''
        _.districtList = []
        _.district = ''
        _.streetList = []
        _.street = ''
      } else if (level === 'city') {
        _.districtList = []
        _.district = ''
        _.streetList = []
        _.street = ''
      } else if (level === 'district') {
        _.streetList = []
        _.street = ''
      }
    },
    // 根据adcode搜索(change事件)
    search: function (adcode, level) {
      let _ = this
      // 清除地图
      map.clearMap()
      districtSearch.setLevel(level) // 行政区级别
      districtSearch.setExtensions('all')
      // 行政区查询
      // 按照adcode进行查询可以保证数据返回的唯一性
      districtSearch.search(adcode, function (status, result) {
        if (status === 'complete') {
          _.getData(result.districtList[0], level)
        }
      })
    },
    downloadGeo: function () {
      let _ = this
      var b = _.bounds // 边界坐标
      if (!b) {
        _.$message({
          type: 'error',
          showClose: true,
          center: true,
          message: '当前未选择城市，无法下载'
        })
        return
      }
      var coods = [] // 坐标
      // 边界GeoJson
      var res = {
        'type': 'FeatureCollection',
        'features': [
          {
            'type': 'Feature',
            'properties': {},
            'geometry': {
              'type': 'Polygon',
              'coordinates': [coods]
            }
          }
        ]
      }
      if (b.length > 1) {
        // 多面
        res.features.geometry.type = 'MultiPolygon'
        for (let i = 0; i < b.length; i++) {
          coods[i] = []
          for (let j = 0; j < b[i].length; j++) {
            const cood = b[i][j]
            coods[i].push([cood.lng, cood.lat])
          }
        }
      } else {
        // 单面
        for (let i = 0; i < b[0].length; i++) {
          const cood = b[0][i]
          coods.push([cood.lng, cood.lat])
        }
      }
      // 保存文件
      var blob = new Blob([JSON.stringify(res)], {
        type: 'text/plain;charset=utf-8'
      })
      FileSaver.saveAs(blob, _.fileName + '.json')
    }
  },
  mounted: function () {
    this.init() // 初始化
  }
}
</script>

<style scoped>
  .container, #amap-container {
    width: 100%;
    height: 100%;
  }
  .citySelect-container {
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 9;
  }
</style>



